@import url(https://fonts.googleapis.com/css2?family=Libre+Baskerville&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{font-family:'Libre Baskerville', serif}.token-container{height:100px;width:100px;border-radius:50%;border:solid 1px black;overflow:hidden}.token-container .token{height:100%;display:flex;justify-content:center;align-items:center;background-size:70%;background-repeat:no-repeat;background-position:center center;background-color:#772f1a}.token-container .token-DIAMOND{border:5px solid #fff;background-image:url(/splendor/static/media/diamond.3a412abc.png)}.token-container .token-ONYX{border:5px solid #000;background-image:url(/splendor/static/media/onyx.1a6421b0.png)}.token-container .token-RUBY{border:5px solid red;background-image:url(/splendor/static/media/ruby.1ccb5125.png)}.token-container .token-GOLD{border:5px solid gold;background-image:url(/splendor/static/media/gold.6237158e.png)}.token-container .token-SAPPHIRE{border:5px solid blue;background-image:url(/splendor/static/media/sapphire.5361b8d5.png)}.token-container .token-EMERALD{border:5px solid green;background-image:url(/splendor/static/media/emerald.84aea5eb.png)}.card-container{min-height:15vh}.card-container .victory-points{font-size:3em}.card-container .cost-container .cost{border:solid 2px black;border-radius:50%;background:orange;height:30px;width:30px;display:flex;justify-content:center;align-items:center}.card-container .cost-container .cost .quantity{color:white}[class^="player-card"]{background:red;border-radius:0px 0px 30px 0px;height:150px;margin-top:1em;position:relative}[class^="player-card"][active="true"]{border:solid 6px orange}[class^="player-card"] .points{background:green;border-radius:50%;height:2em;display:flex;justify-content:center;width:2em;align-items:center;position:absolute;top:-.25em;right:-.25em}

